import React from 'react';
import { Route, Switch } from 'react-router-dom';

import HomePage from './pages/HomePage';
import NUICAppForm from './pages/nuicApplicationForm';
import PaticipantsList from './pages/paticipantsList';

class Routes extends React.Component {
  render() {
    return (
      <Switch>
        <Route exact path='/' component={HomePage} />
        <Route path='/application' component={NUICAppForm} />
        <Route path='/paticipant' component={PaticipantsList} />

        <Route
          render={function () {
            return <>
              <div class="col d-flex justify-content-center pt-5">
                <h1 className='display-1'>404</h1>
              </div>

              <div class="col d-flex justify-content-center pt-5">
                <h1 className=''>You’ve gone off the map</h1>
              </div>
            </>;
          }}
        />
      </Switch>
    );
  }
}

export default Routes;
