import React, { Component } from 'react';
import { MDBContainer, MDBAlert, MDBBtn } from 'mdbreact';
import SectionContainer from '../components/sectionContainer';

class NUICAppForm extends Component {

    render() {
        return (
            <MDBContainer className='mt-5'>
                <MDBAlert color="light text-center" >
                    Welcome userEmailAddress | Welcome back userFullName
                </MDBAlert>

                <SectionContainer header='Personal Infomation'>
                    <form>
                        <div className='form-row'>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>Firstname</label>
                                <input type='text' className='form-control' id='inputEmail4' placeholder='' required />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>Middlename</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>Lastname</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Gender</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your gender</option>
                                    <option value="female">Female</option>
                                    <option value="male">Male</option>
                                </select>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>Date of birth</label>
                                <input type='date' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='UserPhone' readOnly />
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Email address</label>
                                <input type='email' className='form-control' placeholder='UserEmail' readOnly />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Country of residence</label>
                                <select className="browser-default custom-select">
                                    <option value="Nigeria" selected>Nigeria</option>
                                </select>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>State</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your state of residence</option>
                                    <option value="">List of state</option>
                                </select>
                            </div>
                        </div>
                        <button type='submit' className='btn btn-primary btn-md px-3'>
                            Save and contine
                        </button>
                    </form>
                </SectionContainer>

                <SectionContainer header='Educational Information'>
                    <form>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Your institution name</label>
                                <input type='text' className='form-control' id='inputEmail4' placeholder='' required />
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>Program of study</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Year of entry</label>
                                <input type='number' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>Matric or registration number</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Are you currently studying?</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your status</option>
                                    <option value="yes">Yes</option>
                                    <option value="no">No</option>
                                </select>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>Current level of education</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your status</option>
                                    <option value="undergraduate">Undergraduate</option>
                                    <option value="postgraduate">Postgraduate</option>
                                    <option value="graduate">Graduate Alumni</option>
                                </select>
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputPassword4'>
                                    <span>Current level OR Year of entry OR Year of graduation</span>
                                </label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <button type='submit' className='btn btn-primary btn-md px-3'>
                            Save and contine
                        </button>
                    </form>
                </SectionContainer>

                <SectionContainer header='Participants Infomation'>
                    <p className='py-3'>Applying as a team require a minimum of 2 and maximum of 5 team members. Participant can only submit one application at a time. If you are applying as a team, please nominate one person to fill in the application and submit it.</p>
                    <form>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>You are applying as?</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your status</option>
                                    <option value="team">We are applying as a team</option>
                                    <option value="individual">I am are applying as an individual</option>
                                </select>
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>1. Fullname</label>
                                <input type='text' className='form-control' placeholder='UsersFullname' readOnly />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>1. Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='UsersPhoneNumber' readOnly />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>1. Email address</label>
                                <input type='email' className='form-control' placeholder='UsersEmail' readOnly />
                            </div>
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-12'>
                                <small id='helpBlock' className='form-text text-muted'>
                                    Your team members infomation
                                </small>
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>2. Fullname</label>
                                <input type='text' className='form-control' placeholder='' required />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>2. Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='' required />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>2. Email address</label>
                                <input type='email' className='form-control' placeholder='' required />
                            </div>

                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>3. Fullname</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>3. Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>3. Email address</label>
                                <input type='email' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>4. Fullname</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>4. Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>4. Email address</label>
                                <input type='email' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputEmail4'>5. Fullname</label>
                                <input type='text' className='form-control' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>5. Phone number</label>
                                <input type='text' className='form-control' pattern='[0-9]{11}' placeholder='' />
                            </div>
                            <div className='form-group col-md-4'>
                                <label htmlFor='inputPassword4'>5. Email address</label>
                                <input type='email' className='form-control' placeholder='' />
                            </div>
                        </div>
                        <button type='submit' className='btn btn-primary btn-md px-3'>
                            Save and contine
                        </button>
                    </form>
                </SectionContainer>

                <SectionContainer header='Innovative Idea Infomation'>
                    <p className='py-3'>Please share your innovative idea/minimum viable product(s) that can qualify you for a grant.</p>
                    <form>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Category you are applying</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your category</option>
                                    <option value="ideation">Ideation</option>
                                    <option value="mvp">Minimum Viable Product</option>
                                </select>
                                <small id='helpBlock' className='form-text text-muted'>
                                    <b>Ideation</b> - for undergraduates, and recent graduates (max of 4 years).
                                </small>
                                <small id='helpBlock' className='form-text text-muted'>
                                    <b>MVP</b> - for recent alumni, postgraduate, and young researchers (under 30).
                                </small>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Area of interest</label>
                                <select className="browser-default custom-select">
                                    <option selected disabled>Choose your area of intrest</option>
                                    <option>E-commerce Development</option>
                                    <option>Smart AgricTech</option>
                                    <option>Drone Technologies</option>
                                    <option>Mobile Computing and 5G Networks</option>
                                    <option>Satellite Communication Technologies</option>
                                    <option>Renewable Energy Technologies</option>
                                    <option>Media Technologies (VR, MR, and GIS)</option>
                                    <option>Automobile Software Development, and Fabrication</option>
                                </select>
                            </div>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='exampleFormControlTextarea1'>Summarize your innovative idea/MVP in 150 words</label>
                            <textarea className='form-control' id='exampleFormControlTextarea1' rows='3' />
                        </div>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor='inputEmail4'>Video URL</label>
                                <input type='url' className='form-control' placeholder='E.g: https://youtube.com/...' />
                                <small id='helpBlock' className='form-text text-muted'>
                                    YouTube link showcasing your idea/MVP (max of 4 minutes)
                                </small>
                            </div>
                        </div>
                        <p>Only shortlisted participants will be contacted for further instructions. You may be required to register your novel ideas or products with Nigeria Copyright Commission in due courses.</p>
                        <MDBAlert color="info">
                            <h4 className="alert-heading">NOTE</h4>
                            <p>Only shortlisted participants will be contacted for further instructions. You may be required to register your novel ideas or products with Nigeria Copyright Commission in due courses.</p>
                        </MDBAlert>
                        <p>By clicking the submit button, I accept that all the data provided above are true and correct. Any false declaration renders my application invalid. </p>

                        <button type='submit' className='btn btn-primary btn-md px-3'>
                            Save and contine later
                        </button>
                        <button type='submit' className='btn btn-success btn-md px-5 float-right'>
                            Submit
                        </button>
                    </form>
                </SectionContainer>

                <div className='mb-3 float-center text-center'>
                    <MDBBtn color="danger" size="sm">Log me out</MDBBtn>
                </div>
            </MDBContainer >
        );
    }
}
export default NUICAppForm;